body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

h2 {
  color: #288eea; }

.button-font {
  font-size: 14px !important;
  font-weight: bold !important;
  padding: 3px 10px !important;
  margin-bottom: 2px; }

.phonebook-list {
  border: 1px solid #ececec;
  border-radius: 3px;
  padding: 20px;
  margin: 40px 0;
  max-width: 800px;
  font-family: "Helvetica", "sans-serif";
  margin-bottom: 15px; }

.border-list {
  border-bottom: 1px solid #cbffd8;
  margin-top: 10px;
  padding: 10px; }

.form-add {
  padding: 10px;
  padding-left: 0; }

.form-add input {
  margin-left: 105px;
  padding: 4px;
  font-size: 17px; }

input[type=text] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box; }

label {
  font-weight: bold; }

/* 
Filter CSS
*/
.filter {
  margin: 40px 0; }

.filter h2 {
  padding-bottom: 20px; }

.filter-persons {
  padding: 5px 0;
  margin: 5px 0;
  border-bottom: 1px solid #cbffd8; }

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

